




















import Vue from "vue";
import Component from "vue-class-component";

const Props = Vue.extend({
  props: {
    tooltipOn: String,
    tooltipOff: String,
    value: Boolean,
    label: String,
    disabled: Boolean
  },
  model: {
    prop: "value",
    event: "input"
  }
});

@Component
export default class ToolbarSwitch extends Props {
  get tooltip() {
    if (this.value) {
      return this.tooltipOn;
    }
    return this.tooltipOff ? this.tooltipOff : this.tooltipOn;
  }
}
